// pages/api/products/index.js
import CatalogService from "services/catalogService";
import dbConnect from "lib/dbConnect";

export default async function handler(req, res) {
  await dbConnect();
  if (req.method === "GET") {
    const { q, page = 1, limit = 20 } = req.query;
    const result = await CatalogService.searchProducts({ q, page: Number(page), limit: Number(limit) });
    return res.status(200).json(result);
  }
  if (req.method === "POST") {
    try {
      const created = await CatalogService.createProduct(req.body);
      return res.status(201).json(created.toObject ? created.toObject() : created);
    } catch (err) {
      return res.status(400).json({ error: err.message });
    }
  }
  return res.status(405).end();
}
