// src/services/catalogService.js
import ProductModel from "../../models/productModel.js";
import DomainProduct from "../domain/catalog.js";

export default class CatalogService {
  static async createProduct(data) {
    const model = new ProductModel(data);
    const saved = await model.save();
    return DomainProduct.fromDocument(saved);
  }

  static async updateProduct(id, updates) {
    const updated = await ProductModel.findByIdAndUpdate(id, updates, { new: true });
    return updated ? DomainProduct.fromDocument(updated) : null;
  }

  static async getProductById(id) {
    const doc = await ProductModel.findById(id).lean();
    return doc ? DomainProduct.fromDocument(doc) : null;
  }

  static async searchProducts({ q = "", page = 1, limit = 20, inStockOnly = false } = {}) {
    const filter = {};
    if (q) filter.$text = { $search: q }; // requires text index
    if (inStockOnly) filter["sizes.stock"] = { $gt: 0 };

    const skip = (page - 1) * limit;
    const docs = await ProductModel.find(filter).skip(skip).limit(limit).lean();
    const total = await ProductModel.countDocuments(filter);
    const items = docs.map(d => DomainProduct.fromDocument(d));
    return { items, total, page, limit };
  }
}
